import * as ShopApi from '@/api/shop/shop.js';
import config from '@/api/config.js';
import * as MemberApi from '@/api/member/member.js';
export default {
	data() {
		return {
			target: '',
			show1: true,
			show2: false,
			show3: false,
			data_list: [],
			page_index: 1,
			page_count: 0,
			type: 1,
			isChecked:false,
			allChecked:false,
			checkedArr:[],
			checkedIs:false,
			checkedAll:false,
			shelvesArr:[],
		};
	},
	props: {
		shop_id: {
			type: Number,
			default: () => {
				return 0;
			}
		}
	},
	watch: {
		shop_id(e) {
			let _this = this;
			_this.isChecked = false;
			_this.allChecked = false;
			_this.checkedArr = [];
			_this.checkedIs = false;
			_this.checkedAll = false;
			_this.shelvesArr = [];
			_this.page_index = 1;
			_this.getData(_this.page_index);
		}
	},
	filters:{
		filterImg(value){
			if(value){
				return config.domain + value
			}
		},
	},
	methods: {
		clickList(e) {
			if(!e.target.dataset.id)return;
			let target = e.target.dataset.id;
			this.target = target;
			switch (this.target) {
				case 'one':
					this.show1 = true;
					this.show2 = false;
					this.show3 = false;
					this.type = 1;
					break;
				case 'two':
					this.show2 = true;
					this.show1 = false;
					this.show3 = false;
					this.type = 2;
					break;
				case 'three':
					this.show3 = true;
					this.show2 = false;
					this.show1 = false;
					this.type = 3;
					break;
				default:
					this.show1 = true;
			}
			this.page_index = 1;
			this.getData(this.page_index);
		},
		getData(page_index) {
			let _this = this;
			uni.showLoading({
				title: '加载中'
			});
			if (_this.type == 1 || _this.type == 2) {
				this.data_list = []
				ShopApi.getShopCarGoodsList({
					page_index: page_index,
					shop_id: _this.shop_id,
					is_recommend: _this.type == 2 ? 1 : 0
				}).then(res => {
					let data = res.data.data;
					/* for (let index in data) {
							data[index].pic_cover = config.domain + data[index].pic_cover;
					} */
					if (page_index > 1) {
						data = this.data_list.concat(data);
					}
					setTimeout(() => {
						this.data_list = data;
						console.log(this.data_list)
						this.page_count = res.data.page_count;
						uni.hideLoading(this.data_list);
					}, 500);
				});
			} else {
				ShopApi.getShopDealCarGoodsList({
					page_index: page_index,
					shop_id: _this.shop_id
				}).then(res => {
					let data = res.data.data;
					/* for (let index in data) {
						data[index].pic_cover = config.domain + data[index].pic_cover;
					} */
					console.log(data)
					if (page_index > 1) {
						data = this.data_list.concat(data);
					}
					setTimeout(() => {
						this.data_list = data;
						this.page_count = res.data.page_count;
						uni.hideLoading();
					}, 500);
				});
			}
		},
		loadData(e) {
			let _this = this;
			if (_this.page_index < _this.page_count) {
				_this.page_index++;
			} else {
				return;
			}
			_this.getData(_this.page_index);
		},
		goUrl(url,type=0){
			let _this = this;
			_this.$c.goUrl(url,type)
		},
		handleErr(e, index) {
			if (e.type == 'error') {
				this.data_list[index].pic_cover = '/static/images/list.jpg';
			}
		},
		handleDetail(goodsId,group,promotion_id){
			let groupId = parseInt(String(group).substr(0,1));
			this.$c.detailsJump(goodsId,groupId,promotion_id)
		},
		// 选择车源推荐
		changeCheckbox(e){
			console.log(e.detail.value)
			this.checkedArr = e.detail.value;
			if(this.checkedArr.length > 0 && this.checkedArr.length == this.data_list.length){
				this.allChecked = true;
			}else{
				this.allChecked = false;
			}
		},
		// 精品推荐全选
		shelvesChange(e){
			let value = e.detail.value
			if(value.length > 0){
				this.allChecked = true;
				this.data_list.forEach((item) => {
					//console.log(item)
					let itemId = String(item.goods_id);
					if(!this.checkedArr.includes(itemId)){
						this.checkedArr.push(itemId);
					}
				})
				this.isChecked = true;
				console.log(this.checkedArr)
			}else{
				this.isChecked = false;
				this.checkedArr = [];
			}
		},
		// 精品推荐
		handleRecommend(){
			let _this = this;
			console.log(_this.checkedArr)
			MemberApi.setGoodsRecommend({
				goods_ids:_this.checkedArr.join(","),
			})
			.then(res => {
				_this.$c.showLoading('加载中');
				if(res.code > 0){
					setTimeout(() => {
						_this.$c.hideLoading();
						_this.$c.msg("推荐成功")
						_this.getData(_this.page_idnex)
					},500)
					
				}else{
					_this.$c.hideLoading();
					_this.$c.msg("推荐失败")
				}
			})
			
			
		},
		//选择车源取消推荐
		checkboxChange(){
			this.shelvesArr = e.detail.value;
			if(this.shelvesArr.length > 0 && this.shelvesArr.length == this.data_list.length){
				this.checkedAll = true;
			}else{
				this.checkedAll = false;
			}
		},
		// 精品推荐全选下架
		changeShelves(e){
			let value = e.detail.value
			if(value.length > 0){
				this.shelvesArr = true;
				this.data_list.forEach((item) => {
					//console.log(item)
					let itemId = String(item.goods_id);
					if(!this.shelvesArr.includes(itemId)){
						this.shelvesArr.push(itemId);
					}
				})
				this.checkedIs = true;
			}else{
				this.checkedIs = false;
				this.shelvesArr = [];
			}
		},
		// 精品推荐下架
		handleCancel(){
			let _this = this;
			MemberApi.setGoodsCancel({
				goods_ids:_this.checkedArr.join(","),
			})
			.then(res => {
				_this.$c.showLoading('加载中');
				if(res.code > 0){
					setTimeout(() => {
						_this.$c.hideLoading();
						_this.$c.msg("下架成功")
						_this.getData(_this.page_idnex)
					},500)
					
				}else{
					_this.$c.hideLoading();
					_this.$c.msg("下架失败")
				}
			})
			
			
		},
	}
};
